/* Heart Sprite  */

.heart {
  width: 205px;
  height: 100px;
  background: url("https://raw.githubusercontent.com/KesterJosh/Website-SampleX/main/Artboard_1.png")
    no-repeat;
  /* https://aheioqhobo.cloudimg.io/v7/_playground-bucket-v2.teleporthq.io_/14566c4f-9518-40c2-a4d5-e844c14123fc/74396f00-f1c0-4086-a2cc-f16ade4e1bfa?org_if_sml=1&force_format=original */
  background-position: 0 0;
  cursor: pointer;
  transition: background-position 1s steps(5);
  transition-duration: 0s;

  &.is-active {
    transition-duration: 0.4s;
    background-position: -1025px 0;
  }
  &.isNot-active {
    transition-duration: 0.4s;
    background-position: 0px 0;
  }
}

