.transaction-container10 {
  width: 100%;
  display: flex;
  overflow: auto;
  position: relative;
  min-height: 100vh;
  align-items: flex-start;
  flex-direction: row;
  justify-content: flex-start;
  background-image: linear-gradient(
    135deg,
    rgb(250, 250, 250) 1%,
    rgb(214, 214, 214) 100%
  );
}

